<template>
  <div class="form-wrap">
    <Modal
      v-model="modal"
      title="新建账号" :styles="{width:'100%'}" class-name="modal-box">
      <Form class="fu">
        <ul class="form-full">
          <li>
            <span class="label-left">姓名：</span>
            <Input @on-blur='userDataNum' @on-focus='onFocusName' v-model="name" placeholder="请输入姓名"
                   style="width: 300px"></Input>

            <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
          </li>
          <li>
            <span class="label-left">用户名：</span>
            <Input @on-blur='userUserName' @on-focus='onFocusUsername' v-model="userName" placeholder="请输入用户名"
                   style="width: 300px"></Input>
            <span :class="{'label-right-active':isUserAtive}" class="label-right">{{soltUserName}}</span>
          </li>
          <li>
            <span class="label-left">密码：</span>
            <Input @on-blur='passwordSolt' @on-focus='onFocusPaw' v-model="password" type="password"
                   placeholder="请输入密码：" style="width: 300px"></Input>
            <span :class="{'label-right-active':passwordActive}" class="label-right">{{soltPaw}}</span>
          </li>
          <li>
            <span class="label-left">确认密码：</span>
            <Input @on-blur='surePawSolt' @on-focus='onFocusSurePaw' v-model="surePaw" type="password"
                   placeholder="清再次输入密码" style="width: 300px"></Input>
            <span :class="{'label-right-active':surePawSolActivet}" class="label-right">{{soltSurePaw}}</span>
          </li>
          <li>
            <span class="label-left">权限角色：</span>
            <CheckboxGroup v-for='(item,index) in getRoleListarr' :key="index" v-model="roleIdArr" @on-change='checkAllGroupChange'
                           style="display:inline-block">
              <Checkbox class="" :label="item.roleId">{{item.roleName}}</Checkbox>
            </CheckboxGroup>
          </li>
        </ul>
      </Form>

      <div slot="footer">
        <Button @click='cancel' style="margin-right:20px;">取消</Button>
        <Button class="btnColor" type="primary" @click="setUser">新建</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import $ from 'jquery'

  export default {
    data() {
      return {
        modal: false,
        name: '',
        userName: '',
        password: '',
        surePaw: '',
        textArea: '',
        roleIds: [],
        soltNameLen: '最多可输入100个字符',
        soltUserName: '至少4位，最长16位，允许使用大小写英文字母、中文、数字',
        soltPaw: '至少6位，最长32位，必须包含小写字母、大写字母和阿拉伯数字三种字符',
        soltSurePaw: '请重复您上面输入的密码以确认输入正确',
        isAtive: false,
        isUserAtive: false,
        passwordActive: false,
        surePawSolActivet: false,
        radiaData1: 1,
        radiaData2: 2,
        radiaData3: 3,
        radiaData4: 4,
        radiaData5: 5,
        getRoleListarr: [],
        checkedNames: [],
        checked: true,
        roleIdChild: '',
        roleIdArr: []

      }
    },
    created() {
      this.getRoleList();
      this.getroleUserId();
    },
    methods: {
      beforeLoad() {
        this.userName = "";
        this.password = "";
        this.surePaw = "";
        this.roleIdArr = [];
        this.roleIdChild = "";
        this.name = "";
        this.soltNameLen = "最多可输入100个字符";
        this.soltUserName = '至少4位，最长16位，允许使用大小写英文字母、中文、数字';
        this.soltPaw = '至少6位，最长32位，必须包含小写字母、大写字母和阿拉伯数字三种字符';
        this.soltSurePaw = '请重复您上面输入的密码以确认输入正确';
        this.isAtive = false;
        this.isUserAtive = false;
        this.passwordActive = false;
        this.surePawSolActivet = false;
        this.modal = true;
      },
      cancel() {
        this.roleIdArr = [];
        this.userName = "";
        this.password = "";
        this.surePaw = "";
        this.name = "";
        this.roleIdChild = "";
        this.soltNameLen = "最多可输入100个字符";
        this.soltUserName = '至少4位，最长16位，允许使用大小写英文字母、中文、数字';
        this.soltPaw = '至少6位，最长32位，必须包含小写字母、大写字母和阿拉伯数字三种字符';
        this.soltSurePaw = '请重复您上面输入的密码以确认输入正确';
        this.isAtive = false;
        this.isUserAtive = false;
        this.passwordActive = false;
        this.surePawSolActivet = false;
        this.modal = false;
      },
      urlCreate(url){
        this.$Http.get(url, {}).then(res => {
          if (res.body.status === 0) {
            this.modal = false;
            this.$emit("filterParent");
          }
        })
      },
      setUser() {
        const url = "/user/createSysUser.json?userName=" + encodeURIComponent(this.userName)
          + "&password=" + encodeURIComponent(this.password) + "&passwordConfirm=" + encodeURIComponent(this.surePaw)
          + "&roleIds=" + this.roleIdChild + "&name=" + encodeURIComponent(this.name);
          if(this.userName && this.password && this.surePaw && this.roleIdChild && this.name) {
            this.urlCreate(url);
          }
      },
      // 获取用户角色list
      getRoleList() {//
        const url = "/common/getRoleList.json?";
        this.$Http.get(url, {}).then(res => {
          console.log(res.body.data.roleList);
          this.getRoleListarr = res.body.data.roleList;

        })
      },
      userDataNum() {
        //姓名位数不在0-100间
        if (this.name) {
          var reg = /^[\s\S]{1,100}$/;
          var r = this.name.match(reg);
          if (r == null) {
            this.soltNameLen = "姓名长度超过一百个字符"
            this.isAtive = true;
          }
        } else {
          this.soltNameLen = "姓名不能为空"
          this.isAtive = true;
        }
      },
      userUserName() {
        //用户名
        if (this.userName) {
          console.log(this.soltUserName)
          var reg = /^[a-zA-Z0-9_-]{4,16}$/;
          var r = this.userName.match(reg);
          console.log(r)
          if (r == null) {
            this.soltUserName = "用户名不符合标准"
            this.isUserAtive = true;
          }
        } else {
          this.soltUserName = "用户名不能为空"
          this.isUserAtive = true;
        }
      },
      passwordSolt() {
        if (this.password) {
          var reg = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{6,32}$/gm;
          var r = this.password.match(reg);
          if (r == null) {
            this.soltPaw = "密码格式不正确"
            this.passwordActive = true;
          }
          if (this.password == this.surePaw) {
            this.soltSurePaw = "密码确认正确"
            this.surePawSolActivet = false
          } else {
            this.soltSurePaw = "两次密码输入不同"
            this.surePawSolActivet = true
          }
        } else {
          this.soltPaw = "密码不能为空"
          this.passwordActive = true;
        }
      },
      surePawSolt() {
        if (this.surePaw) {
          if (this.surePaw == this.password) {
            this.soltSurePaw = "密码确认正确"
          } else {
            this.soltSurePaw = "两次密码输入不同"
            this.surePawSolActivet = true
          }
        } else {
          this.soltSurePaw = "密码不能为空"
          this.surePawSolActivet = true;

        }

      },
      onFocusName() {
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;

      },
      onFocusUsername() {
        this.soltUserName = "至少4位，最长16位，允许使用大小写英文字母、中文、数字"
        this.isUserAtive = false;

      },
      onFocusPaw() {
        this.soltPaw = "至少6位，最长32位，必须包含小写字母、大写字母和阿拉伯数字三种字符"
        this.passwordActive = false;
      },
      onFocusSurePaw() {
        this.soltSurePaw = "请重复您上面输入的密码以确认输入正确"
        this.surePawSolActivet = false
      },
      getroleUserId() {
        // $(".Check-box").change(function() {
        // 	alert($(this).checked);
        // });

      },
      checkAllGroupChange(data) {

        let str = "";
        for (var i = 0; i < data.length; i++) {
          if (i == data.length - 1) {
            str += data[i]
          } else {

            str += data[i] + ","
          }
        }
        this.roleIdChild = str
        console.log(this.roleIdChild);
      },


    }
  }
</script>
<style scoped>
  .label-right-active {
    color: red !important;
  }

  .btn-wrap-folder {
    position: fixed;
    bottom: 50px;
  }

  .label-left {
    display: inline-block;
    width: 80px;
  }
</style>



