<!-- 账号管理 -->

<template>
  <div>
    <top-title title="账号管理"></top-title>

    <div class="fu">
      <div class="btn-wrap">
        <div>
          <Button class="btnColor" icon="plus" type="primary" @click="thisRouter">新建账号</Button>
          <Button type="text" v-on:click="showFilter">筛选&nbsp;&nbsp;<Icon type="arrow-down-b"></Icon>
          </Button>
        </div>
        <div>
          <Input v-model="value" placeholder="请输入姓名或用户名" style="width: 300px"></Input>
          <Button class="btnColor" type="primary" icon="search" @click='search'>搜索</Button>
        </div>

      </div>
      <div class="master-content-middle" v-show="isFilterShow">
        <div class="label">
          状态：
        </div>
        <div class="label-value">
          <Select v-model="status" style="width:200px" @on-change="search">
            <Option value="-99">全部</Option>
            <Option value="1">正常</Option>
            <Option value="0">已删除</Option>
          </Select>
        </div>

      </div>

      <div class="master-content-content" style="margin-top: 10px;">
        <Table :loading="loading" border ref="selection" :columns="columns" :data="data1"
               @on-selection-change="selectData"></Table>
        <Page @on-change='clickPage' :total="pageSize" show-total></Page>
      </div>
      <Modal
        v-model="modal1"
        title="删除用户？"
        @on-ok="ok"
        @on-cancel="cancel">
        <p>确定要删除此条内容吗？</p>
      </Modal>
    </div>
    <set-Folder ref="setFolder" v-on:filterParent="roleList"></set-Folder>
    <recompose-Role ref="recomposeRole" @filterParent="roleList"></recompose-Role>
  </div>


</template>

<script>
  import PageSize from '../../../common/PageSize'
  import topTitle from '@/components/common/topTitle'
  import setFolder from './child/setFolder.vue'
  import recomposeRole from './child/recomposeRole.vue'

  export default {
    components: {topTitle, setFolder, recomposeRole},
    data() {
      return {
        value: '',
        status: "1",
        modal: false,
        loading: true,
        data1: [],
        routeParams: '',
        pageSize: 0,
        pageNo: '',
        modal1: false,
        bolOk: null,
        paramsIndex: '',
        dataSysUserId: '',
        isFilterShow: false,
        isAdds: true,
        isDeletes: true,
        addCustomParams: [],
        deleteCustomParams: [],

        columns: [
          {
            title: '姓名',
            key: 'name'
          },
          {
            title: '用户名',
            key: 'userName'
          },
          {
            title: '权限角色',
            key: 'rolePermission'
          },
          {
            title: '状态',
            key: 'status',
            render: (h, params) => {
              var status;
              if (params.row.status == 1) status = '正常';
              else status = '已删除';
              return h('span', status)
            }
          },
          {
            title: '操作',
            key: 'operate',
            width: 150,
            align: 'center',
            render: (h, params) => {
              if (params.row.status == 0) {
                return;
              }
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  // attrs:{
                  //     "class":'btnRecompose'
                  // },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      this.$refs.recomposeRole.beforeLoad();
                      this.$refs.recomposeRole.getSysUserById(params.row.sysUserId)
                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {
                      // this.modal1 = true;
                      // this.paramsIndex = params.index;
                      // this.dataSysUserId = params.row.sysUserId;

                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);
                    }
                  }
                }, '删除')
              ]);
            }

          }

        ],
      }
    },
    created() {
      this.roleList();
    },
    methods: {
      showFilter: function () {
        this.isFilterShow = !this.isFilterShow;
      },
      selectData: function (selection) {
        console.log(selection)
        let canDeletes = selection.filter((item) => {
          return item.status == 1;
        });

        let canAdds = selection.filter((item) => {
          return item.status == 0;
        });
        console.log(canAdds)
        this.isAdds = !canAdds.length > 0;
        alert(this.isAdds)
        this.addCustomParams = canAdds;
        this.isDeletes = !canDeletes.length > 0;
        this.deleteCustomParams = canDeletes;
      },

      deleteBox(params) {
        console.log(params);
        let SysUserNames = params.map((item, index) => {
          return item.name;
        });
        let SysUserIds = params.map((item, index) => {
          return item.sysUserId;
        });
        this.$Modal.confirm({
          title: '删除账号',
          content: `你确定要删除` + SysUserNames.join(","),
          onOk: () => {
            // alert(SysUserIds)
            this.deleteMaterialData(SysUserIds.join(","));
            console.log(SysUserIds.join(","))
          }
        })
      },

      deleteMaterialData(sysUserId) {

        const url = "/user/deleteSysUser.json?sysUserId=" + sysUserId;
        console.log(sysUserId)
        this.$Http.get(url, {}).then(res => {
          console.log(res)
          this.$Message.info(res.body.data);
          this.roleList();
          // if (res.body.status === 0) {
          //  this.data.splice(index, 1);
          //  this.$Message.info(res.body.msg);
          // }else{
          //  this.$Message.info(res.body.msg);
          // }

        })
      },


      show(index) {
        this.$Modal.info({
          title: 'User Info',
          content: `Name：${this.data1[index].name}<br>Age：${this.data1[index].age}<br>Address：${this.data1[index].address}`
        })
      },
      remove(index) {
        this.data1.splice(index, 1);
      },
      roleList() {
        const userName = sessionStorage.getItem('userName');

        // console.log(userName);
        const url = "/user/getSysUserList.json?userName=" + encodeURIComponent(userName) + "&userNameOrNameLike=" + encodeURIComponent(this.value)
          + "&status=" + this.status + "&pageSize=10&pageNo=" + this.pageNo
        this.$Http.get(url, {}).then(res => {
          console.log(res.body.data);
          const arr = res.body.data.result;
          let tablList = [];
          this.pageSize = res.body.data.totalCount;
          this.loading = false;
          this.data1 = [];
          for (var key in arr) {
            // console.log(arr[key].roles)
            let rolesIdArr = arr[key].roles;
            console.log(rolesIdArr);
            for (var key2 in rolesIdArr) {
              console.log(rolesIdArr[key2].id)
            }
            tablList = {
              'name': arr[key].name,
              'userName': arr[key].userName,
              'rolePermission': arr[key].rolesDisp,
              'sysUserId': arr[key].sysUserId,
              'status': arr[key].status
            }
            // this.data1 = [];
            this.data1.push(tablList);


          }
          // console.log(tablList);
        })
      },
      asyncOK() {
        setTimeout(() => {
          this.modal = false;
        }, 2000);
      },
      thisRouter() {
        // this.$router.push({path: '/setFolder'})
        this.$refs.setFolder.beforeLoad();
      },
      clickPage(data) {
        console.log(data);
        this.loading = true;
        this.pageNo = data;
        this.data1 = [];
        this.roleList();
      },
      search() {
        // alert(this.value)
        this.loading = true;
        this.data1 = [];
        this.roleList()
      },
      ok() {
        const url = "/user/deleteSysUser.json?sysUserId=" + this.dataSysUserId;
        console.log(url);
        this.$Http.get(url, {}).then(res => {
          console.log(res);
          if (res.body.status == 0) {
            this.$Message.info(res.body.msg);
            this.data1.splice(this.paramsIndex, 1);
          } else if (res.body.status == 200) {
            this.$router.push({name: 'login'});
            this.$Message.info(res.body.msg);
          } else {
            this.$Message.info(res.body.msg);
          }


        })
      },
      cancel() {
        this.$Message.info('已取消删除');
      }
    }
  }
</script>

<style scoped>
  .fu {
    padding: 10px;
  }

  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
  }

  .btn-wrap {
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
  }

  .master-content-middle {
    display: -webkit-flex; /* Safari */
    display: flex;
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    align-items: center;
  }
</style>



